#posts-list {
  max-width: 600px;
  margin: auto;

  h2 {
    margin-bottom: 2rem;
    text-align: center;
  }
  article {
    max-width: 550px;
    margin: 0 auto;
    margin-bottom: 4rem;

    h3 {
      margin-bottom: 1rem;
    }

    #author {
      text-align: end;
      font-style: italic;
    }
  }
}

#post-form {
  max-width: 600px;
  margin: auto;

  h2 {
    margin-bottom: 2rem;
    text-align: center;
  }

  form {
    max-width: 550px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;

    input,
    textarea,
    select {
      outline: black solid 1px;
      margin-bottom: 2rem;
    }

    button {
      max-width: fit-content;
      padding: 0.7rem 1.5rem;
      cursor: pointer;
      margin: 0 auto;

      &:hover {
        background-color: aliceblue;
      }
    }
  }
}

.reaction-buttons {
  display: flex;
  gap: 1rem;

  .reaction-button {
    background: white;
    cursor: pointer;
  }
}
